<template>
  <div
    class="the-header-futures-contracts the-header-inner__search"
    :class="{ 'is-opened': contractsVisibility }"
    v-click-outside="() => { contractsVisibility = false }"
  >
    <div class="the-header-inner__search-wrapper" @click="toggleContractsVisibility">
      <div
        class="the-header-inner__search-selected"
        @click="handleInputAreaClick"
      >
        <div class="the-header-inner__search-selected-name-wrapper">
          <!--<div class="the-header-inner__search-selected-name-icon">-->
            <!--<e-icon :src="$options.icons.iconFuturesContract" />-->
          <!--</div>-->

          <div class="the-header-inner__search-selected-name-content">
            <div class="the-header-inner__search-selected-name">
              {{contractName.replace(/USDT|USD/g, '')}}
            </div>
            <!--<div class="the-header-inner__search-selected-token">-->
              <!--<div class="the-header-inner__search-selected-token-code">-->
                <!--<span v-if="contractBaseToken">-->
                  <!--{{ contractBaseToken.shortName }} - {{ contractBaseToken.name }}-->
                <!--</span>-->
                <!--<span v-else>&#45;&#45;</span>-->
              <!--</div>-->
            <!--</div>-->
          </div>
        </div>

        <div class="the-header-inner__search-selected-info-wrapper">
          <!--<div class="the-header-inner__search-selected-info-date-wrapper">-->
            <!--<div class="the-header-inner__search-selected-info-label">-->
              <!--<e-tooltip position="right">-->
                <!--<template slot="label">-->
                  <!--Expires-->
                <!--</template>-->
                <!--<template slot="message">-->
                  <!--The date on which this contract will expire and will settle according to the price from the price source at that moment. The date is approximate and tied to a specific block number. The time of the block can vary slightly.-->
                <!--</template>-->
              <!--</e-tooltip>-->
            <!--</div>-->
            <!--<div class="the-header-inner__search-selected-info-value">-->
              <!--{{expiresApproxAt}}-->
            <!--</div>-->
          <!--</div>-->
          <div class="the-header-inner__search-selected-info-price-wrapper">
            <div class="the-header-inner__search-selected-info-label">
              <e-tooltip position="right">
                <template slot="label">
                  Price
                </template>
                <template slot="message">
                  The Mark Price is the current price from the contract's price source.
                </template>
              </e-tooltip>
            </div>
            <div class="the-header-inner__search-selected-info-value -price">
              <e-big-number :value="activeContract.markPrice" :decimals="FUTURES_PRICE_DECIMALS" :decimal-places="2"/>
              USD
            </div>
          </div>
          <!--<div class="the-header-inner__search-selected-info-leverage-wrapper">-->
            <!--<div class="the-header-inner__search-selected-info-label">-->
              <!--<e-tooltip position="right">-->
                <!--<template slot="label">-->
                  <!--Margin-->
                <!--</template>-->
                <!--<template slot="message">-->
                  <!--The level of exposure for this contract. The margin for buy orders is shown in green, while the margin for sell orders is red. A 5x margin means that you can open a position five times greater than the collateral used.-->
                <!--</template>-->
              <!--</e-tooltip>-->
            <!--</div>-->
            <!--<div class="the-header-inner__search-selected-info-value">-->
              <!--<div class="the-header-inner__search-selected-info-leverage-value">-->
                <!--<div class="the-header-inner__search-selected-info-leverage-value-item -increase">-->
                  <!--<e-big-number :value="activeContract.unavailable" :decimal-places="1"/>X-->
                <!--</div>-->
                <!--<div class="the-header-inner__search-selected-info-leverage-value-item -decrease">-->
                  <!--<e-big-number :value="activeContract.unavailable" :decimal-places="1"/>X-->
                <!--</div>-->
              <!--</div>-->
            <!--</div>-->
          <!--</div>-->
          <!--<div class="the-header-inner__search-selected-info-leverage-wrapper">-->
            <!--<div class="the-header-inner__search-selected-info-label">-->
              <!--<e-tooltip position="right">-->
                <!--<template slot="label">-->
                  <!--Range-->
                <!--</template>-->
                <!--<template slot="message">-->
                  <!--The range represents the upper and lower limit on the price for this contract. A range of 200-300 means that the minimum price for this contract is 200$, while the maximum price is 300$. If the Mark Price goes outside the range, the contract automatically settles not waiting for the expiration date.-->
                <!--</template>-->
              <!--</e-tooltip>-->
            <!--</div>-->
            <!--<div class="the-header-inner__search-selected-info-value">-->
              <!--<e-big-number :value="activeContract.unavailable" :decimals="FUTURES_PRICE_DECIMALS" :decimal-places="2"/>-->
              <!-- - -->
              <!--<e-big-number :value="activeContract.unavailable" :decimals="FUTURES_PRICE_DECIMALS" :decimal-places="2"/>-->
            <!--</div>-->
          <!--</div>-->
        </div>
      </div>

      <label
        class="the-header-inner__search-close"
        :class="{
          'the-header-inner__search-close-active': contractsVisibility
        }"
        @click.stop
      >
        <e-icon :src="$options.icons.iconArrow"/>
      </label>
    </div>

    <transition name="search">
      <div
        v-if="contractsVisibility"
        class="the-header-inner__search-results"
      >
        <the-header-futures-result
          @select="selectContract"
          @mousedown.native.prevent
        />

        <div v-if="demoStep === 4" class="the-header-inner__demo">
          <e-demo title="Assets" arrowPositionMobile="top" :nextAction="hideContracts">
            This is the list of all available assets with information about the last price, 24 hour volume and 24 hour price change.
          </e-demo>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
import { mapGetters, mapState } from 'vuex';
import moment from 'moment';

import { setTourStep } from '@/api/backend/statistics';

import EIcon from '@/components/EIcon';
import EBigNumber from '@/components/EBigNumber';
import ETooltip from '@/components/ETooltip';
import TheHeaderFuturesResult from '@/containers/blocks/TheHeaderFuturesResult';
import EDemo from '@/components/EDemo';

import iconFuturesContract from '@/icons/e-icon__futures-contract.svg';
import iconArrow from '@/icons/e-icon_arrow_down.svg';
import iconLink from '@/icons/e-icon__link2.svg';

export default {
  name: 'the-header-futures-contracts',

  data() {
    return {
      contractsVisibility: false,
      isFocused: false,
    };
  },

  icons: {
    iconFuturesContract,
    iconArrow,
    iconLink,
  },

  computed: {
    ...mapGetters({
      activeWallet: 'wallets/activeWallet',
      demoStep: 'ui/demoStep',
      contractBaseToken: 'futuresActiveContract/contractBaseToken',
      contractName: 'futuresActiveContract/contractName',
    }),

    ...mapState({
      activeContract: state => state.futuresActiveContract.activeContract,
    }),

    isActiveTokenDisplayed() {
      return !this.isFocused && this.displayedActiveToken;
    },

    /**
     * If user changes active token, we display new active token before it's initialized.
     */
    displayedActiveToken() {
      return this.contractBaseToken;
    },

    expiresApproxAt() {
      return this.activeContract
        ? moment(this.activeContract.expiresApproxAt).format('MMMM DD')
        : '...';
    },
  },

  watch: {
    demoStep() {
      if (this.demoStep === 4) {
        setTimeout(() => {
          this.contractsVisibility = true;
        }, 100);
      }
    },
  },

  methods: {
    handleFocus() {
      this.isFocused = true;
    },

    handleBlur() {
      this.isFocused = false;
    },

    toShortenedAddress(a) {
      return `${a.slice(0, 4)}…${a.slice(-4)}`;
    },

    handleInputAreaClick() {
      const searchEl = this.$refs.search;
      if (searchEl) {
        searchEl.focus();
      }
    },

    blurInput() {
      const searchEl = this.$refs.search;
      if (searchEl) {
        searchEl.blur();
      }
    },

    toggleContractsVisibility() {
      this.contractsVisibility = !this.contractsVisibility;
    },

    hideContracts() {
      if (this.demoStep) {
        setTourStep({ walletAddress: this.activeWallet.address, step: this.demoStep });
        this.$store.commit('ui/SET_DEMO_STEP', this.demoStep + 1);
      }
      this.contractsVisibility = false;
    },

    selectContract(futuresContractHash) {
      this.$router.push({ name: 'future', params: { contract: futuresContractHash } });
      this.blurInput();
      this.contractsVisibility = false;
    },
  },

  components: {
    EIcon,
    TheHeaderFuturesResult,
    EBigNumber,
    ETooltip,
    EDemo,
  },
};
</script>

<style lang="scss">
  @import '~@/scss/styles';

  .the-header-futures-contracts {
    $this: &;

    @include rmin(1280) {
      display: none;
    }
  }

  .the-header-inner {
    $this: &;

    &__search-results {
      /*width: calc(100vw - 240px - 88px - 300px);*/
      width: 780px;
      min-width: 780px;

      @include rmin(1921) {
        /*width: calc(100vw - 240px - 88px - 404px);*/
      }

      @include r(1460) {
        /*width: calc(100vw - 240px - 88px - 300px);*/
      }

      @include r(1279) {
        width: 100%;
        min-width: 100%;
        position: relative;
      }
    }

    &__demo {
      position: absolute;
      z-index: 20;
      top: 0;
      right: 0;
      transform: translate(calc(100% + 10px), 0);

      @include r(1279) {
        top: auto;
        bottom: 0;
        left: 0;
        right: auto;
        transform: translate(10px, calc(100% - 130px));
      }
    }
  }
</style>



